export default {
	data() {
		return {
			codeMsg: '发送验证码',
			count: 10,
			disable: false,
			checked: false,
		};
	},
	methods:{
		clickCodemsg(){
			if(this.disable == false){
				let timer =  setInterval(() => {
					this.count -= 1
					this.codeMsg = this.count + 's'
					this.disable = true
					if(this.count < 0){
						clearInterval(timer)
						this.codeMsg = '发送验证码'
						this.count = 10
						this.disable = false
					}
				}, 1000)
			}
		},
		gotoUrl(url){
			uni.navigateTo({
				url:url
			})
		},
		loginForProvider(type){
			var vm = this;
			let openid = uni.getStorageSync('openid')
			uni.getProvider({
			    service: 'oauth',
			    success: function (res) {
			        if (~res.provider.indexOf('weixin')) {
			        	uni.login({
			        		provider: 'weixin',
			        		success: function(loginRes) {
								console.log(loginRes)
			        			vm.authorization = loginRes.code // 获取code
								uni.getUserInfo({
									provider: 'weixin',
									success: (info) => { //这里请求接口
									  console.log(info);
									},
									fail: () => {
									  uni.showToast({title:"微信登录授权失败",icon:"none"});
									}
								  })
			        		},
			        		fail(err) {
			        			uni.showToast({
			        				title: '授权失败！',
			        				icon: 'none'
			        			});
			        		}
			        	});
			        }else{
						uni.showToast({
							title: '请先安装微信或升级版本',
							icon:"none"
						});
					}
			    }
			});
		},
		changeCheckbox(e){
			this.checked = !this.checked
		},
		openProvider(){
			uni.showToast({
				title: '请先同意服务协议！',
				icon: 'none'
			});
		}
	}
}
